import "./Navbar.css";
import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <div className="navbar">
        <div className="logo-box">
            
            <div className="logo">
                <Link to="/" >
                    
                    <p><div className="logo-m">M</div>
                    <div className="logo-n">N</div>
                    <div className="logo-k">K</div>
                    <div className="logo-p">.</div></p>
                </Link>
            </div>
            <div className="b3">
                    |||
            </div>            
        </div>
        <div className="navlinks">

            <ul>
                <Link to="/" className="bb-slider">
                    <li>Home</li>
                </Link>
                <Link to="/resume" className="bb-slider">
                    <li >About</li>
                </Link>
                <Link to="/projects" className="bb-slider">
                    <li>Projects</li>
                </Link>
                <Link to="/contact" className="bb-slider">
                    <li>Contact</li>
                </Link>
            </ul>            
        </div>

    </div>
  );
}

export default Navbar;